import React, { useEffect, useState, FC } from "react";

import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";

import { getManifest } from "api/missionManifests";
import Manifest, { getEmptyManifest } from "entities/Manifest";
import RoverSelector from "components/custom/RoverSelector/roverSelector";
import RoversName from "src/entities/RoversName";

const ManifestInfo: FC<{ roverName: string }> = ({ roverName }) => {
  const [manifest, setManifest] = useState<Manifest>(getEmptyManifest());

  useEffect(() => {
    getManifest(roverName).then((apiResp) => {
      setManifest(apiResp);
    });
  }, [roverName]);

  return (
    <Grid container spacing={4}>
      <Grid item xs={6}>
        <Typography variant='h5' align='center' color='text.secondary' paragraph>
          Name of the Rover:
        </Typography>
      </Grid>
      <Grid item xs={6}>
        <Typography variant='h5' align='center' color='text.secondary' paragraph>
          {manifest.name}
        </Typography>
      </Grid>
    </Grid>
  );
};

export default ManifestInfo;

